import React, { useEffect, useRef, useState } from 'react';
import cctvImage from './cctv.png';
import cctvImage2 from './cctv2.jpg';
import cctvImage3 from './cctv3.jpg';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import { columns } from "./columns";
import {Drawer} from "@mui/material";

const MainPage = () => {
    const mapElement = useRef(null);
    const { naver } = window;

    const rows = [
        { id: 1, object: "", latitude: "37.548769", longitude: "127.120038", address: "   83-66", image: cctvImage },
        { id: 2, object: "2", latitude: "37.521302", longitude: "126.984974", address: "   185", image: cctvImage2 },
        { id: 3, object: "3", latitude: "37.552973", longitude: "126.984190", address: "  2", image: cctvImage3 },
        { id: 4, object: "4", latitude: "37.493379", longitude: "126.919990", address: "  20 33" },
        { id: 5, object: "5", latitude: "37.564087", longitude: "126.891791", address: "   482" },
        { id: 6, object: "6", latitude: "37.526674", longitude: "126.934719", address: "   330" }
    ];

    const [map, setMap] = useState(null);
    const [drawerOpen, setDrawerOpen] = useState(false);
    const [selectedRow, setSelectedRow] = useState(null);

    // const [myLocation, setMyLocation] = useState({
    //     latitude: 37.4979517, //  =  
    //     longitude: 127.0276188,
    // });

    const [myLocation, setMyLocation] = useState({
        latitude: 37.4979517, //  =  
        longitude: 127.0276188,
    });

    useEffect(() => {
        const updateLocation = () => {
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(
                    (position) => {
                        setMyLocation({
                            latitude: position.coords.latitude,
                            longitude: position.coords.longitude,
                        });
                    },
                    () => {
                        //       ()
                        setMyLocation({
                            latitude: 37.4979517,
                            longitude: 127.0276188,
                        });
                    }
                );
            }
        };

        updateLocation();   //     

        const intervalId = setInterval(updateLocation, 60000);  // 1      setInterval 

        return () => clearInterval(intervalId); //     
    }, []);

    const seoulCenter = {   //       .
        latitude: 37.5505,
        longitude: 126.9780,
    };

    useEffect(() => {
        if (!mapElement.current || !naver) return;

        const location = new naver.maps.LatLng(seoulCenter.latitude, seoulCenter.longitude);
        const mapOptions = {
            center: location,
            zoom: 12,
            zoomControl: true,
        };

        const newMap = new naver.maps.Map(mapElement.current, mapOptions);


        rows.forEach((row) => {
            new naver.maps.Marker({
                position: new naver.maps.LatLng(parseFloat(row.latitude), parseFloat(row.longitude)),
                map: newMap,
                title: row.object,
            });
        });

        setMap(newMap);
    }, [naver]);

    const handleObjectClick = (params) => {
        const row = rows.find(r => r.id === params.id);
        if (row) {
            const location = new naver.maps.LatLng(parseFloat(row.latitude), parseFloat(row.longitude));
            map.setCenter(location);
            map.setZoom(20);  //   

            //    
            setSelectedRow(row);
            setDrawerOpen(true);
        }
    };

    return (
        <React.Fragment>
            <div className='header-website'>
                <h1 className='title-website'>   </h1>
                <select className='button'>
                    <option> </option>
                    <option> </option>
                    <option> </option>
                    <option></option>
                    <option></option>
                </select>
            </div>
            <div ref={mapElement} className='map-naver-view'></div>
            <Box>
                <DataGrid
                    rows={rows} // rows    .
                    columns={columns}
                    onCellClick={handleObjectClick}
                    initialState={{
                        // pagination: {
                        //     paginationModel: {
                        //         pageSize: 5
                        //     },
                        // },   //  rows     .  DataGrid    100   .
                    }}
                    // pageSizeOptions={[5]}
                    checkboxSelection
                    disableRowSelectionOnClick
                />
            </Box>
            <Drawer
                anchor='left'
                open={drawerOpen}
                onClose={() => setDrawerOpen(false)}
            >
                {selectedRow && (
                    <Box p={2} width="250px" textAlign="center">
                        <h2>{selectedRow.object}</h2>
                        <img src={selectedRow.image} alt="Placeholder" style={{ width: '100%', height: 'auto' }} />
                        <p>{selectedRow.address}</p>
                    </Box>
                )}
            </Drawer>
        </React.Fragment>
    );
};

export default MainPage;